	import uniPopup from '@/components/uni-popup/uni-popup.vue'
	import uniPopupMessage from '@/components/uni-popup/uni-popup-message.vue'
export default{
	data(){
		return {
			array:[],
			page:1,
			pageSize:10,
			hasMoreData:true
		}
	},
	onLoad() {
		this.initLeaveRecord();
	},
	methods:{
		//获取请假记录
		initLeaveRecord(){
			uni.showLoading({
				title:"加载中"
			})
			var app=getApp().globalData;
			var that=this;
			uni.request({
				url:app.url+"askLeave/getLeaveList",
				header: {
						// //设置参数内容类型为json
						'Content-Type': 'application/x-www-form-urlencoded',
						Authorization:app.token // 默认值
					  },
			  data: {
					classID:app.globalclassID,
					status:"0,1",
					curpage:that.page,
					pagesize:that.pageSize,
				  },
			  method: 'POST',
			  dataType: 'json',
			  success:(res)=>{
				  var arr=res.data;
				  uni.hideLoading();
				  console.log(res.data);
				  if(arr=="reLogin"){
					  uni.showModal({
					  	title:"提示",
					  	content:"登录过期，请重新登录",
					  	showCancel:false,
					  	success:function(res) {
					  		if(res.confirm){
					  			uni.reLaunch({
					  				url:"../login/login"
					  			})
					  		}
					  	}
					  })
				  }else{
					  if(arr.result==true){
						  for(var item of arr.data){
						  	item.IMAGE="data:image/png;base64,"+item.IMAGE.replace(/[\r\n]/g,"");
						  }
					  	if(arr.data!=undefined){
					  		if(that.page==1){
					  			that.array=arr.data;
					  		}else{
					  			that.array=that.array.concat(arr.data)
					  		}
					  		if(that.array.length>=arr.count){
					  			that.hasMoreData=false;
					  		}else{
					  			that.hasMoreData=true;
					  		}
					  	}
					  }else{
					  	that.leaveArray=[];
					  }
				  }
			  },
			  fail:(res)=>{
				  uni.hideLoading();
				  that.msg="服务器异常!";
				  this.$refs.oftenProblem.open();
			  }
			})
		}
	}
	
}
